nav{
    background: rgba(0, 0, 0, 0.2);
    width: max-content;
    display: block;
    border-radius: 3rem;
    padding: 0.2rem 1.2rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    gap: 0.8rem;
    backdrop-filter: blur(15px);
}

nav a{
    background: transparent;
    padding: 0.3rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
}
nav a:hover{
    background: rgba(0, 0, 0, 0.3);
}

nav a.active{
    background: var(--color-bg);
    color: white;
}